/**
 * @description: æ¸é¤ç©ºæ ¼åæ¢è¡
 */
export const clearBr = (str = '') => {
  if (str.length === 0) return '';
  return str
    .replace(/\s+/g, '')
    .replace(/<\/?.+?>/g, '')
    .replace(/[\r\n]/g, '');
};
/**
 * å¨ææå¥script/linkæ ç­¾
 * @param {Array | String} url script/linkçurléå
 * @param {Element} appendee æå¥çç¶åç´  é»è®¤body
 * @param {Function} callback ææscript onloadåè° ä¹å¯éè¿è¿åçpromiseæ§è¡åè°
 */
export const scriptOnLoad = (urls, appendee, callback) => {
  urls = Array.isArray(urls) ? urls : [urls];
  const array = urls.map((src) => {
    const cssReg = /\w*.css$/;
    let script;
    if (cssReg.test(src)) {
      const link = document.createElement('link');
      link.type = 'text/css';
      link.rel = 'stylesheet';
      link.href = src;
      script = link;
    } else {
      script = document.createElement('script');
      script.type = 'text/javascript';
      script.src = src;
    }
    const bodyElement = document.getElementsByTagName('body')[0];
    const currentAppendee = appendee || bodyElement;
    currentAppendee.appendChild(script);
    return new Promise((resolve) => {
      script.onload = () => {
        resolve();
      };
    });
  });

  return new Promise((resolve) => {
    Promise.all(array).then(() => {
      if (typeof callback === 'function') {
        callback();
      }
      resolve();
    });
  });
};
